import * as util from "./util.js"
import { MBP ,Buffer} from 'meta-buffer-pack'
import EventEmitter from "eventemitter3/umd/eventemitter3.js";
import { CMD } from './constants.js'
  
const encoder = new TextEncoder()
const decoder = new TextDecoder()
const NB = MBP.NB
const MB = MBP.MB
const MBA = MBP.MBA

export class Remocon extends EventEmitter{
  constructor(url , protocols =[]) {
    super();
    this.ws;
    this.lsid = Remocon.getID(); //local Remocon instance index id.
    this.ssid = 0; // serverside websocket ID
    this.protocols = protocols 
    this.lastState = false
    this.maxPayload = 1000;
    this.serverURL = url;
    this.nick = "";
    this.txCounter = 0;
    this.rxCounter = 0;
    this.lastTxRxTime = Date.now();
    this.lastTxRxTimeOut = 65000;  // timeout after trx.
    this.runCheckPeriod = 3000 ;//not ping period, only check state. + +Math.trunc(Math.random() * 2000);
    this.runCheckIntervalID = null;
    this.isNode = false;
    this.channels = new Set()
    this.promiseMap = new Map()
    this.promiseTimeOut = 2000
    this.mid = 0
  
    if (typeof WebSocket === "undefined") this.isNode = true;
    Remocon.ls.add(this);
    this.connect();

  }

  static ls = new Set();
  static ID = 1;
  static NodeWebSocket;
  static CMD = CMD
  static getID() {
    return Remocon.ID++;
  }
  static getByteSize(data){
    return util.getByteSize(data)
  }
  static timeStamp(){
    return util.timeStamp()
  }
  static buf2hex(data){
    return util.buf2hex(data)
  }
  static u8concat(...data){
    return MBP.U8pack(...data) 
  }
  static PU8(data){
    return MBP.U8(data)
  }

  runChecker(url) {
    // console.log('.')
    if (this.ws?.readyState != 1) {
      this.connect();
    } else {
      let time = Date.now() - this.lastTxRxTime;
      if (time > this.lastTxRxTimeOut ) {
        this.echo();
        // this.echoFrame()
      }
    }

  }

  close() {
    let code = 1000;
    let reason = "well done";
    this.ws?.close(code, reason);
    this.ws = null;
    clearInterval(this.runCheckIntervalID);
    this.runCheckIntervalID = null
  }

  open(url){
    this.connect(url)
  }

  connect(url) {
    if (url) this.serverURL = url;

   if(this.runCheckIntervalID)  clearInterval(this.runCheckIntervalID);
   this.runCheckIntervalID = setInterval(this.runChecker.bind(this), this.runCheckPeriod);

   console.log('+')
    this.ws?.close()
    this.ws = null;
 
    if (this.isNode) {
      this.ws = new Remocon.NodeWebSocket(this.serverURL   , this.protocols);
      this.ws.on("message", this.onWebSocketMessage.bind(this));
    } else {
      this.ws = new WebSocket(this.serverURL , this.protocols );
      this.ws.addEventListener("message", this.onWebSocketMessage.bind(this));
    }
    this.ws.binaryType = "arraybuffer";

    this.ws.onopen = (e) => {
      this.subscribeChannelsToServer();        
      if( !this.lastState ) this.emit("statechange", "open" );
      this.lastState = true
      this.emit("open", "open" );
    };
  
    this.ws.onerror = (e) => {  

      // console.log('e.error', e )
      // console.log('e.error', e.error )
    if(this.isNode)  console.log('e.error message', e.message )
      // console.log('e.type', e.type )

    };

    this.ws.onclose = (e) => {
      console.log('e.close  code,reason', e.code , e.reason )

      if( this.lastState ) this.emit("statechange","close");
      this.lastState = false
      this.emit("close",  "close" );
      if (e.code === 1009) console.log("전송 크기 초과. Over maxPayload size");
    };


  } //end connect 

  subscribeChannelsToServer(){
    //binary sub 
    if(this.channels.size > 0){
      let chList = Array.from(  this.channels )
      let chEncoded = encoder.encode( chList.join(','))
      this.send( Buffer.concat([
        NB('8', CMD.sub ),   // PU8( CMD.sub )
        NB('16', ++this.mid),
        NB('16',chEncoded.byteLength),
        chEncoded ]) )
      
    }

    // text sub frame
    // if(this.channels.size > 0){
    //   let chList = Array.from(  this.channels )
    //   let subFrame = ['sub'].concat( chList )
    //   console.log('send sub frame', subFrame)
    //   this.sendJSON( subFrame )
    // }

  }

  // hybrid event data listener.
  // In Node:  EventEmitter.emit( data, isBinary) , first argument is data itself.
  // In Browser:  EventTarget.distpatchEvent( event: MessageEvent),  data property of event(one argument) is data.  ( event.data)
  onWebSocketMessage(event ,isBinary ) {
    this.rxCounter++;
    this.lastTxRxTime = Date.now();
    let message ;
    let cmd = []
    if (this.isNode) {
      message = event;
      if(!isBinary){
      //In Node: It's Buffer not a String
        message = new TextDecoder().decode(message)
      }
    } else {
      message = event.data;
      isBinary = (message instanceof ArrayBuffer)

    }

// console.log('raw message', message ,isBinary)
// console.log('type of message', typeof message )
 
    if (isBinary) {
      //type = "arraybuffer";  // client binaryType is arraybuffer.  
      this.binaryMessageParser( Buffer.from( message) ) 
      
    } else {
      try {  
        let jsonObj = JSON.parse(message);
        if(  Array.isArray( jsonObj)  ){
          cmd = jsonObj
          console.log('('+ cmd[0] + ') ' )
          switch(cmd[0] ){
            case 'pub':  //publish, 1:channelName , 2:message
              if( cmd[1] && cmd[2] ){
                this.emit(cmd[0], ...cmd  );  // pub event
                cmd.shift()
                this.emit(cmd[0], ...cmd );  // channel event
              } 
              break;
            case 'ssid':  //ssid from server, 1:ssid
              if( cmd[1] ){
                this.ssid = cmd[1];
              } 
            case 'echo':  //echo, 1:msg
            case 'iam':  //iam, 1:iaminfo
            case 'auth':
              this.emit( cmd[0], ...cmd );  
              break;
            
          }
          // Policy:  Remocon client  do not filter frame messages. 
          // dispatch 'frameName' event and 'message' event both.
        }
      } catch (error) {   // !JSON.  
      }

    }

    // every raw message.  
    this.emit("message",  message ,isBinary );
    
  }

  binaryMessageParser( buffer ){
   let cmd = buffer[0];
   let cmdName = '['+ CMD[ cmd ] +'] '
  //  console.log(cmdName );
   switch( cmd){
      case CMD.res:
        let code = buffer.readUint8(1)
        let mid = buffer.readUint16BE(2)
        let meta = ( buffer.byteLength > 4  ) ?  buffer.subarray(4) : ""
        console.log(`[RES] MID: ${mid} CODE: ${code} `)
        this.testPromise( mid , code , meta)
        break;
      case CMD.pub: 
        let binObj = MBP.unpack( buffer )
        let evtName = decoder.decode( binObj.chInfo)
        let params = binObj.args 
        this.emit( evtName,evtName, ...params)
        break;
      case CMD.svr:

        console.log( MBP.unpack( buffer ).$ )
        break;
      case CMD.pub1: 
        if( buffer.byteLength >= 6){
          let msgID = buffer.readUint16BE(2)
          let chLen = buffer.readUint16BE(4)
          if( buffer.byteLength >= 6 + chLen){
            let chData = buffer.slice(6, 6+chLen)
            let chInfo = decoder.decode(chData)
            console.log('BIN_PUB1 chInfo', chInfo)
            let payload = buffer.slice( 6 + chLen )
            try {
              let decoded = decoder.decode( payload) 
              let jsObj = JSON.parse( decoded )
              this.emit( chInfo , chInfo, ...jsObj)
            } catch (error) {
              console.log('Invalid. PUB frame. mid:', msgID)
            }
          }
      }
      break;

    }
  }

 
  echoFrame( ...args ){
    if(args.length > 0 ){
      args.unshift('echo')
      this.frame( ...args)
    }else{
      this.frame("echo")
    }
  }

  echo( ...args ){
    if(args.length > 0 ){
      this.bin( CMD.echo, ...args)
    }else{
      this.bin( CMD.echo )
    }
  }

  loop( ...args){ 
    args.unshift('loop')
    this.sendJSON( args)
  }

  frame( ...args){
    this.sendJSON( args )
  }

  sendJSON( msg ){
    this.send( JSON.stringify( msg ))
  }

  send(data) {
    // let len = util.getByteSize(data);
    // if (len === 0) return;

    // if (len > this.maxPayload) {
    //   console.log(" maxPayLoad over:", len);
    //   return;
    // }

    if (this.ws?.readyState === 1) {
      // console.log('send', data)
      this.txCounter++;
      this.lastTxRxTime = Date.now();
      this.ws.send(data);
    } else {
      // don't use  this.connect() , reson: too much try connect.
    }
  }

  bin(...data){
    this.send( Remocon.u8concat( ...data) )
  }

  
  setMsgPromise(mid ){
    return new Promise( (resolve, reject)=>{
      this.promiseMap.set( mid, [resolve, reject ] )
      // console.log('set promise.  mid, size', mid, this.promiseMap.size)
      setTimeout( e=>{ 
        if(this.promiseMap.has(mid )){
          reject('timeout');
          this.promiseMap.delete( mid )
          // console.log('promise timeout. mid, size:', mid, this.promiseMap.size)
        }
      }, this.promiseTimeOut);
    })
  }

  testPromise(mid , code , metaPack){
    if( this.promiseMap.has(mid)){
      console.log('res promise msg', mid)
      let [ resolve, reject ] = this.promiseMap.get( mid )
      this.promiseMap.delete( mid )
      // console.log(util.buf2hex(metaPack.buffer))
      let meta = MBP.unpack( metaPack)
      if(code < 128){
        // console.log( 'unpack meta:', meta)
        resolve( meta )
      } else{
        reject( meta )
      } 
      
    }else{
      // console.log('no promise')
    }
  }

  auth(...args){
    let mid = ++this.mid;
    this.send(  MBP.pack( 
      MB('cmd','8',CMD.auth) , 
      MB('mid','16', mid), 
      MBA( ...args) ))
      return  this.setMsgPromise( mid )
  }
  // publish. return promise
  publish(chInfo, ...args ){
      let mid = this.pub( chInfo, ...args)
      return  this.setMsgPromise( mid )
  }

//publish only. return mid.
  pub(chInfo, ...args ){
    if( typeof chInfo !== 'string') throw 'first parameter : channelName should be string.'
    let chEncoded = encoder.encode( chInfo)
    this.send(  MBP.pack( 
      MB('#cmd','8',CMD.pub) , 
      MB('#mid','16',++this.mid), 
      MB('#chLen','16', chEncoded.byteLength),
      MB('chInfo', chEncoded),
      MBA( ...args) ))
    // console.log('bin.pub ch:', chInfo)
    return this.mid
  }
 
    
  subscribe(chInfo, handler){
    let mid = this.sub( chInfo, handler)
    return  this.setMsgPromise( mid ) 
  }

  sub(chInfo, handler){
    if( typeof chInfo !== 'string') throw 'first parameter : channelName should be string.'
    let chEncoded = encoder.encode( chInfo) 
    this.send( Buffer.concat( [
      NB('8',CMD.sub),  
      NB('16', ++this.mid), 
      NB('16', chEncoded.byteLength), 
      chEncoded ]) )
    this.channels.add(chInfo)
    if(handler){  // ch#tag
      this.on( chInfo , handler)
    }
    // console.log('bin.sub ch:', chInfo)
    return this.mid 
  }


  publishFrame( channelName, ...data ){
    this.frame(  'pub', channelName,  ...data   )
  }

  subscribeFrame(channelName, handler) {
    this.channels.add(channelName)
    this.frame('sub',channelName)
    if(handler){
      this.on( channelName , handler)
    }
  }

  unsubscribeFrame(channelName){
    this.channels.delete(channelName)
    this.removeAllListeners( channelName )
  }

 
}




